import { FragmentTrack } from '../type'

export default function createFragmentTrack(): FragmentTrack {
  return {
    trackId: 0,
    baseDataOffset: 0n,
    defaultSampleDuration: 0,
    defaultSampleSize: 0,
    defaultSampleFlags: 0,
    baseMediaDecodeTime: 0n,
    sampleCount: 0,
    dataOffset: 0,
    dataOffsetPos: 0n,
    firstSampleFlags: 0,
    sampleDurations: [],
    sampleSizes: [],
    sampleFlags: [],
    sampleCompositionTimeOffset: [],
    baseIsMoof: false,
    ioWriter: null,
    buffers: []
  }
}
